/*eslint-disable*/

import React from 'react';
import './business.css';

function Business() {
    window.addEventListener('scroll' , function(){
        this.scrollY > 200
        ? $('.topBtn').addClass('on')
        : $('.topBtn').removeClass('on')
    });

    const moveTop = () => {document.documentElement.scrollTo({top: 0, behavior: 'smooth'})};

    return(
        <div>
            <div className="busTop">
                BUSINESS
            </div>

            <div className="topBtn" onClick={moveTop}></div>
        </div>
    );
}


export default Business;