import React from 'react';
import Footer from "./Footer";

import { Link, Route, Switch } from 'react-router-dom';

import "./bigdata.css";

function Bigdata() {
    return(
        <div className="bigContainer">
            <div id="lnb">
                <Link to="/solution/1">딥러닝솔루션</Link>
                <Link to="/solution/2" className="on">빅데이터솔루션</Link>
                <Link to="/solution/3">클라우드</Link>
            </div>
    
            <div className="bigInfo">
                <h2><span>빅데이터솔루션</span>BIGDATA SOLUTION</h2>
                <p>
                    SNS 소셜 데이터, 웹 데이터 크롤링, 사용자 정보, 회사 보유 데이터 등 다양한 빅데이터를 수집 후 분석합니다. <br /> 
                    이 사용자 중심의 정보를 통해 산업 모델링, 시각화, 컨설팅까지 전 주기적 솔루션을 가능하게 하는 기술입니다.
                </p>
            </div>

            <div className='bigSkills'>
                <h3>보유기술</h3>
                <div>
                    contents
                </div>
            </div>
            <Footer />
        </div>
    );
}

export default Bigdata;