/*eslint-disable*/

import React,{ useRef } from 'react';
import "./Contact.css";
import emailjs from '@emailjs/browser';
import Map from './Map';

import Pin from '../images/location.png';
import Call from '../images/call.png'

import Footer from './Footer';

import $ from 'jquery';

function Contact() {
    window.addEventListener('scroll' , function(){
        this.scrollY > 200
        ? $('.topBtn').addClass('on')
        : $('.topBtn').removeClass('on')
    });

    const moveTop = () => {document.documentElement.scrollTo({top: 0, behavior: 'smooth'})};

    const form = useRef();

    const sendEmail = (e) => {
        e.preventDefault();

        emailjs.sendForm('service_t0nbzj9', 'template_ycsjzbu', form.current, 'user_BVUdObmqMT09wD1BxfQmJ')
        .then((result) => {
            console.log(result.text);
            document.getElementById('contactForm').reset();
        }, (error) => {
            console.log(error.text);
        });
    };
        return ( 
            <div className='conContainer'>
                <div className='conTop'>
                    CONTACT
                </div>
                <div className='conContents'>
                    <div className='location'>
                        <h2>LOCATION</h2>
                        <div>
                            <Map />
                            <div><img src={Pin} alt="pinIcon" /><p>부산광역시 수영구 감포로 54, 마로인빌딩 3층 301호</p></div>
                            <div><img src={Call} alt="pinIcon" /><p>051-717-2503</p></div>
                        </div>
                    </div>
                    <div className='conForm'>
                        <h2>CONTACT</h2>
                        <form ref={form} onSubmit={sendEmail} id='contactForm'>
                            <div>
                                {/* <label>Name</label> */}
                                <input className='reset' type="text" name="user_name" placeholder='Your name' />
                            </div>
                            <div>
                                {/* <label>Email</label> */}
                                <input type="email" name="user_email" placeholder='example@email.com' />
                            </div>
                            <div>
                                {/* <label>Message</label> */}
                                <textarea name="message" placeholder='Message' />
                            </div>
                            <input type="submit" value="Send" />
                        </form>
                    </div>
                </div>
                
                <div className="topBtn" onClick={moveTop}></div>
                <Footer />
            </div>
    );
}


export default Contact;