import React, { useEffect, useState } from 'react';
import './App.css';

import { Link, Route, Switch } from 'react-router-dom';

import Main from './components/Main';
import Company from './components/Company';
import Solution from './components/Solution';
import Business from './components/Business';
import Contact from './components/Contact';

import Viva from './components/Viva';
import Info from './components/Info';
import His from './components/His';

import Deeplearning from './components/Deeplearning';
import Bigdata from './components/Bigdata';
import Cloud from './components/Cloud';

import Businfo from './components/Businfo';
import Installex from './components/Installex';
import Product from './components/Product';

function App() {  
  return (
    <div className="App">
      <header>
        <h1><a onClick={() => {window.location.href="/"}}></a></h1>
        <nav>
          <ul id='gnb'>
           <li>
             <a onClick={() => {window.location.href="/company/1"}}>COMPANY<div className='underBar'></div></a>
           </li>
           <li>
             <a onClick={() => {window.location.href="/solution/1"}}>SOLUTION<div className='underBar'></div></a>
           </li>
           <li>
             <a onClick={() => {window.location.href="/business/1"}}>BUSINESS<div className='underBar'></div></a>
           </li>
          
           <li>
             <a onClick={() => {window.location.href="/contact"}}>CONTACT<div className='underBar'></div></a>
           </li>
          </ul>
        </nav>
      </header>

      <Switch>
        <Route exact path="/">
          <Main />
        </Route>

        <Route path="/company/1">
          <Company />
          <Info />
        </Route>
        <Route path="/company/2">
          <Company />
          <Viva />
        </Route>
        <Route path="/company/3">
          <Company />
          <His />
        </Route>

        <Route path="/solution/1">
          <Solution />
          <Deeplearning />
        </Route>
        <Route path="/solution/2">
          <Solution />
          <Bigdata />
        </Route>
        <Route path="/solution/3">
          <Solution />
          <Cloud />
        </Route>

        <Route path="/business/1">
          <Business />
          <Businfo />
        </Route>
        <Route path="/business/2">
          <Business />
          <Installex />
        </Route>
        <Route path="/business/3">
          <Business />
          <Product />
        </Route>

        <Route path="/contact">
          <Contact />
        </Route>
      </Switch>
            
    </div>
  );
}

export default App;


